* {
  box-sizing: border-box;
}

html {
  color: #2f4f4f;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

body {
  margin: 0;
}

.people {
  --gridColumnCount: 2;
}

.people .container {
  grid-template-columns: repeat(var(--gridColumnCount), 200px);
  place-content: center;
  place-items: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 1000px) {
  .people .container {
    --gridColumnCount: 4;
  }
}

.person {
  transition: transform .3s linear 1s;
  transition: border-radius .3s linear 0;
  transition: background-color .3s linear 0;
  padding: 10px;
}

.person .photo {
  position: relative;
}

.person .image {
  border-radius: 100%;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  transition: border-radius .3s linear 1s;
  display: block;
}

.person .rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person .details {
  flex-direction: column;
  align-items: center;
  gap: 9px;
  display: flex;
}

.person .extra {
  color: #000;
  opacity: 0;
  font-size: 18px;
  font-weight: 400;
}

.person:hover {
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.2);
}

.person:hover .person .extra {
  opacity: 1;
}

.person:hover .person .image {
  border-radius: 60px;
}
